<script>
import vueble from "vueble";
import Chart from "chart.js";
export default {
  name: "App",
  components: {
    vueble
  },
  data() {
    return {
      confirmed: 0,
      puis: 0,
      pums: 0,
      recovered: 0,
      deaths: 0,
      tests: 0,
      dataByFacility: null,
      dataByResidence: null,
      dataByHistory: null
    };
  },
  methods: {
    viewPatientDetails: function() {
      alert("Still under development");
    },
    fetchSummary: async function() {
      const getSummaryMethod = await this.apiGet(
        "https://services5.arcgis.com/mnYJ21GiFTR97WFg/arcgis/rest/services/slide_fig/FeatureServer/0/query?f=json&where=1=1&groupByFieldsForStatistics=ObjectId,day,confirmed,PUIs,PUMs,recovered,deaths,tests&orderByFields=ObjectId&outStatistics=[{%22statisticType%22:%22count%22,%22onStatisticField%22:%22ObjectId%22,%22outStatisticFieldName%22:%22value%22}]&db=slide_fig",
        {}
      );
      const summaryDetails = getSummaryMethod.data.features[0].attributes;

      this.confirmed = summaryDetails.confirmed;
      this.puis = summaryDetails.PUIs;
      this.pums = summaryDetails.PUMs;
      this.recovered = summaryDetails.recovered;
      this.deaths = summaryDetails.deaths;
      this.tests = summaryDetails.tests;
    },
    fetchListByHealthFacilities: async function() {
      const getListByHealthFacilities = await this.apiGet(
        "https://services5.arcgis.com/mnYJ21GiFTR97WFg/arcgis/rest/services/conf_fac_tracking/FeatureServer/0/query?f=json&where=1=1&returnGeometry=false&spatialRel=esriSpatialRelIntersects&outFields=*&orderByFields=count_%20desc&resultOffset=0&resultRecordCount=9999&cacheHint=true",
        {}
      );
      this.dataByFacility = getListByHealthFacilities.data.features;
    },
    fetchListByResidences: async function() {
      const getListByResidences = await this.apiGet(
        "https://services5.arcgis.com/mnYJ21GiFTR97WFg/arcgis/rest/services/PH_masterlist/FeatureServer/0/query?f=json&where=1=1&returnGeometry=false&spatialRel=esriSpatialRelIntersects&outFields=*&groupByFieldsForStatistics=residence&orderByFields=value%20desc&outStatistics=[{%22statisticType%22:%22count%22,%22onStatisticField%22:%22FID%22,%22outStatisticFieldName%22:%22value%22}]&cacheHint=true",
        {}
      );
      this.dataByResidence = getListByResidences.data.features;
    },
    fetchListByHistory: async function() {
      const getListByHistory = await this.apiGet(
        "https://services5.arcgis.com/mnYJ21GiFTR97WFg/arcgis/rest/services/PH_masterlist/FeatureServer/0/query?f=json&where=1%3D1&returnGeometry=false&spatialRel=esriSpatialRelIntersects&outFields=*&orderByFields=FID%20asc&resultOffset=0&resultRecordCount=9999&cacheHint=true",
        {}
      );
      this.dataByHistory = getListByHistory.data.features.sort(function(a, b) {
        return a.attributes.sequ - b.attributes.sequ;
      });
    },
    last: function(array, n) {
      if (array == null) return void 0;
      if (n == null) return array[array.length - 1];
      return array.slice(Math.max(array.length - n, 0));
    },
    createDailyStatChart: async function() {
      const getDailyStatsMethod = await this.apiGet(
        "https://services5.arcgis.com/mnYJ21GiFTR97WFg/arcgis/rest/services/confirmed/FeatureServer/0/query?f=json&where=1%3D1&returnGeometry=false&spatialRel=esriSpatialRelIntersects&outFields=*&orderByFields=date%20asc&resultOffset=0&resultRecordCount=2000&cacheHint=true",
        {}
      );
      const getDailyStats = getDailyStatsMethod.data.features;

      var listOfDates = [];
      var listofValuesAdmitted = [],
        listofValuesRecovered = [],
        listofValuesDeath = [];
      var ddd;
      getDailyStats.forEach(data => {
        ddd = new Date(data.attributes.date).toLocaleDateString("en-US");
        listOfDates.push(ddd);
        listofValuesAdmitted.push({
          t: ddd,
          y: data.attributes.admitted
        });
        listofValuesRecovered.push({
          t: ddd,
          y: data.attributes.recovered
        });
        listofValuesDeath.push({
          t: ddd,
          y: data.attributes.deaths
        });
      });

      const ctx = document.getElementById("covid-chart-daily-stats");
      new Chart(ctx, {
        type: "line",
        data: {
          labels: this.last(listOfDates, 7),
          datasets: [
            {
              label: "Recovered",
              data: this.last(listofValuesRecovered, 7),
              lineTension: 0.1,
              backgroundColor: "transparent",
              borderColor: "#2ecc71",
              pointBackgroundColor: "#27ae60",
              pointBorderColor: "#fff",
              pointHoverBackgroundColor: "#fff",
              pointHoverBorderColor: "rgba(204, 204, 204, 1)"
            },
            {
              label: "Deceased",
              data: this.last(listofValuesDeath, 7),
              lineTension: 0.1,
              backgroundColor: "transparent",
              borderColor: "#e74c3c",
              pointBackgroundColor: "#c0392b",
              pointBorderColor: "#fff",
              pointHoverBackgroundColor: "#fff",
              pointHoverBorderColor: "rgba(204, 204, 204, 1)"
            }
          ]
        },
        options: {
          title: {
            display: true,
            text: "Case Trend"
          },
          scales: {
            xAxes: [
              {
                type: "time",
                distribution: "series"
              }
            ]
          }
        }
      });
    },
    createAgeGroupChart: async function() {
      const getAgeGroupStatsMethod = await this.apiGet(
        "https://services5.arcgis.com/mnYJ21GiFTR97WFg/arcgis/rest/services/age_group/FeatureServer/0/query?f=json&where=1%3D1&returnGeometry=false&spatialRel=esriSpatialRelIntersects&outFields=*&groupByFieldsForStatistics=age_categ%2Csex&outStatistics=%5B%7B%22statisticType%22%3A%22count%22%2C%22onStatisticField%22%3A%22FID%22%2C%22outStatisticFieldName%22%3A%22value%22%7D%5D&cacheHint=true",
        {}
      );
      const getAgeGroupStats = getAgeGroupStatsMethod.data.features;
      var categories = [];
      var dataMale = [];
      var dataFemale = [];
      getAgeGroupStats.forEach(data => {
        if (!categories.includes(data.attributes.age_categ))
          categories.push(data.attributes.age_categ);
      });
      categories.forEach(d1 => {
        getAgeGroupStats.forEach(d2 => {
          if (d2.attributes.age_categ == d1) {
            console.log(d2);
            if (d2.attributes.sex == "Male") dataMale.push(d2.attributes.value);
            else dataFemale.push(d2.attributes.value);
          }
        });
      });

      var barOptions_stacked = {
        title: {
          display: true,
          text: "Age Group"
        },
        tooltips: {
          enabled: true
        },
        hover: {
          animationDuration: 0
        },
        scales: {
          xAxes: [
            {
              stacked: true
            }
          ],
          yAxes: [
            {
              stacked: true
            }
          ]
        }
      };

      var ctx = document.getElementById("covid-chart-age-group");
      new Chart(ctx, {
        type: "horizontalBar",
        data: {
          labels: [
            "10 to 19",
            "20 to 29",
            "30 to 39",
            "40 to 49",
            "50 to 59",
            "60 to 69",
            "70 to 79",
            "80 to 89",
            "90 to 99"
          ],

          datasets: [
            {
              label: "Male",
              data: dataMale,
              backgroundColor: "#ff6b6b",
              hoverBackgroundColor: "#ee5253"
            },
            {
              label: "Female",
              data: dataFemale,
              backgroundColor: "#0abde3",
              hoverBackgroundColor: "#48dbfb"
            }
          ]
        },

        options: barOptions_stacked
      });
    }
  },
  mounted() {
    this.createDailyStatChart();
    this.createAgeGroupChart();
  },
  created() {
    this.fetchSummary();
    this.fetchListByHealthFacilities();
    this.fetchListByResidences();
    this.fetchListByHistory();
  }
};
</script>
